@import url("https://fonts.googleapis.com/css?family=Patrick+Hand+SC|Roboto&display=swap");
@font-face {
  font-family: myFirstFont;
  /* src: url("./src/assets/Kanit\ Light.ttf"); */
  src: url("../src/assets/Kanit\ Light.ttf");

}
body {
  margin: 0;
  padding: 0;
  font-family: myFirstFont;
  overflow: hidden;
}
p {
  font-size: 18px;
}
.App {
  max-width: 800px;
  width: 800px;
  padding: 20px;
  height: 480px;
  max-height: 480px;
  margin: auto;
  border: 1px solid black;
  text-align: center;
  position: relative;
  -ms-overflow-style: none; /* for Internet Explorer, Edge */
  scrollbar-width: none; /* for Firefox */
  overflow-y: scroll;
}
p{
  font-size: 1.2rem;
}
.box-type {
  padding: 10px;
}
h2{
  font-weight: bold;
}
.title-number{
  font-weight: bold;
  font-size: 1.4rem;
}
.bbtnt{
  font-size: 1.2rem;
  font-weight: bold;
}
.box-detail {
  height: 80px;
}
.btn-index {
  height: 55px;
  font-weight: bold;
  font-size: 1.5rem;
}
.bbox {
  position: relative;
}
.detail-text {
  border: 1px solid black;
  border-radius: 10px;
  padding: 20px;
  margin: 10px;
  width: 480px;
  height: 420px;
  position: fixed;
  z-index: 10;
}
.textde {
  text-align: start;
}
textarea {
  margin-top: 10px;
  resize: none;
  height: 400px;
}
.bin-back {
  margin: auto;
  width: 95px;
  font-weight: bold;
  font-size: 1.2rem;
  /* height: 35px; */
  padding: 0.2rem 0.8rem;
  position: fixed;
  z-index: 3;
  top: 435px;
}
.simple-keyboard {
  max-width: 850px;
}
.box-keyborad {
  width: 755px;
  margin: 2px;
  padding: 5px;
  height: 50px;
  text-align: end;
}
.kbb{
  margin-top: -20px;
  height: 250px;
}
.dis-keybord {
  top: 0px;
  position: fixed;
  padding: 10px;
  height: 40px;
  border: 1px solid black;
}
.body-keybord {
  width: 790px;
  margin-left: -200%;
  position: relative;
  /* position: fixed;
  z-index: 3; */
}
.btn-down-keybord{
  position: absolute;
  z-index: 50;
  top: -40px;
  border-radius: 5px;
  border:none;
  right: 3px;
  /* right: ; */
  background-color: rgb(245, 73, 73);
  padding: 5px;
  color: rgb(253, 253, 253);
}
.btn-insert-down{
  background-color: rgb(245, 73, 73);
  padding: 5px;
  color: rgb(253, 253, 253);
  border-radius: 5px;
  border:none;
  margin-bottom: 2px;
}
/* hide scrollbar but allow scrolling */

.App::-webkit-scrollbar {
  display: none; /* for Chrome, Safari, and Opera */
}
.textarea-insert {
  height: 100px;
}
.btn-delete {
  position: absolute;
  right: 10px;
  bottom: 10px;
}
.dp-nonne {
  display: none;
}
